import React, { Component } from 'react';
import axios from 'axios';
import './App.css';

// react select library
import Select from 'react-select';
import Gist from 'react-gist';

class App extends Component {

  // State
  state = {

    // Single State
    todos: [],
    selectedOption: null,
    username: '',
    loading: true,
    firsttime: true,
    found: true,
    singlegistloaded: null
  };


  onChange = (e) => {
    this.setState({username: e.target.value});
    this.setState({singlegistloaded: false});
  }
  

  
  // load gist
  loadGist = (e, user) => {
    e.preventDefault();
    this.setState({ 
      loading: true,
      firsttime: false
    });
    this.setState({username: this.state.username});

    axios.get('https://api.github.com/users/' + this.state.username + '/gists').then(response => { 
      const todos = response.data;


      let newarr = todos.map( item => ({value: item.id, label:item.files[Object.keys(item.files)[0]].filename}))

      this.setState({ 
        todos: newarr,
        loading: false,
        found: true,
        selectedOption: false
      });
    })
    .catch(error => {
      this.setState({
        loading: false,
        found: false
      });
    });

    
  }
  
  handleChange = (selectedOption) => {
    this.setState({ selectedOption });
    this.setState({singlegistloaded: selectedOption.value});
  }

  onMenuClose = (e) => {
    this.setState({singlegistloaded: false});
  }


  render() {
    const { selectedOption } = this.state;

    const username = this.state.username;
    const firsttime = this.state.firsttime;
    const loading = this.state.loading;
    const found = this.state.found;

    

    var loadingMarkup = '';

    if(found === false) {
      loadingMarkup = <p className="warning">User not found</p>
    } else {

      if(firsttime === true) {
        loadingMarkup = <p className="instructions">Please type your <strong>Github username</strong> and press <strong>Load gist</strong> button</p>
      } else {
        if(loading === true) {
          loadingMarkup = <p className="loading">Loading</p>
          
        } else {
          loadingMarkup = <div><h3>{username}'s Gists</h3> <Select options={this.state.todos} value={selectedOption} onChange={this.handleChange} onMenuOpen={this.onMenuClose} /></div>;
        }
      }
    }


    
    
    return (
      <div className="App">
        <h1>Gist Fatcher</h1>

        <form onSubmit={this.loadGist}>
          <input onChange={this.onChange} name="user" value={this.state.username} type="search" placeholder="Type github username"/>
          <input type="submit" value="Load gists"/>
        </form>

        <div id="loading-wrapper">
        {loadingMarkup}
        </div>
        
        {this.state.singlegistloaded ? <div id="single-gist"><Gist id={this.state.singlegistloaded}/></div> : null}

        
      </div>
    );
  }
}

export default App;